import { ColaboradorService } from './../../services/domain/colaborador.service';
import { ColaboradorDTO } from './../../models/colaborador.dto';
import { RecuperaSenhaPage } from './../recupera-senha/recupera-senha';
import { AuthService } from './../../services/auth.service';
import { CredenciaisDTO } from './../../models/credenciais.dto';
import { Component, ViewChild } from '@angular/core';
import { NavController, IonicPage, MenuController, Content } from 'ionic-angular';

@IonicPage()
@Component({
  selector: 'page-home',
  templateUrl: 'home.html'
})
export class HomePage {

  @ViewChild(Content) content: Content;

  creds : CredenciaisDTO = {
    email: "",
    senha: ""
  };

  constructor(public navCtrl: NavController, public menu: MenuController, public auth: AuthService) {

  }

  ionViewDidLoad(){
    this.setDisableScroll(true);
  }

  private setDisableScroll(disable: boolean) : void {
    let scroll = this.content.getScrollElement();
    scroll.style.overflowY = disable ? 'hidden' : 'scroll';
  }
  
  ionViewWillEnter() {
    this.menu.swipeEnable(false);
  }

  ionViewDidLeave() {
    this.menu.swipeEnable(true);
  }

  ionViewDidEnter(){
    this.auth.refreshToken().subscribe(response => {
      this.auth.successfullLogin(response.headers.get('Authorization'),[]);
      this.navCtrl.setRoot('DashboardPage');  
    },
    error => {});
  }

  login(){
    this.auth.authenticate(this.creds).subscribe(response => {
      this.auth.successfullLogin(response.headers.get('Authorization'),[]);
      this.navCtrl.setRoot('DashboardPage');  
    },
    error => {});
  
  }

  signup(){
    this.navCtrl.push('SignupPage');  
  }

  recuperarSenha(){
    this.navCtrl.push('RecuperaSenhaPage');  
  }

}



